body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#navigation {
  padding: 20px;
}

.navtab {
  padding: 0px 40px 0px 10px;
  border-right: 2px solid black;
  font-size: 30px;
}
.bodycontainer {
  height: 100%;
  width: 100%;
  padding: 10px;
  border: 2px solid black;
  border-radius: 5px;
}

.newprodinput {
  padding: 10px;
  font-size: 20px;
}

.newprodbox {
  padding: 10px;
  border: 2px solid black;
}

.newprodlabel {
  font-weight: bold;
  margin-right: 5%;
  font-size: 20px;
}

#newprodbtn {
  background-color: green;
  color: white;
  padding: 10px 40px;
}

#newproddelbtn {
  background-color: red;
  color: white;
  padding: 10px 40px;

}

#newprodupdbtn {
  background-color:blue;
  color: white;
  padding: 10px 40px;
}
.tile {
  height: 100%;
  width: 15%;
  padding: 20px;
  border: 2px solid black;
  display: inline-block;
  vertical-align: top;
  margin: 10px;
}

.tileimg {
  height: 50%;
  width: 95%;
}
.tileedit {
  background-color: orange;
  padding: 5px 40px;
  font-size: 20px;
  font-weight: bold;
  display: block;
}

.tiledelete {
  background-color: red;
  padding: 5px 40px;
  font-size: 20px;
  font-weight: bold;
  display: block;
}


